body {
  background-color: oklch(100% 0 0deg);
  font-family: "Helvetica Neue", helvetica, arial, sans-serif;
  color: var(--text-color);
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
}

h1 {
  font-size: 4rem;
  font-weight: 700;
  line-height: 4.8rem;
}

h2 {
  font-size: 3.2rem;
  font-weight: 600;
  line-height: 4rem;
}

h3 {
  font-size: 2.8rem;
  font-weight: 600;
  line-height: 3.2rem;
}

h4 {
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 2.8rem;
}

h5 {
  font-size: 2rem;
  font-weight: 600;
  line-height: 2.4rem;
}

html {
  height: 100%;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

.o-scroll-margin {
  scroll-margin: 11rem;
}

.o-container {
  max-width: 1024px;
  margin-inline: auto;
}

.grid {
  display: grid;
  grid-template-rows: 1fr repeat(2, auto) 4.5rem repeat(3, auto) 1fr;
  grid-template-columns: 1fr repeat(3, auto) 1fr;
  min-height: 100vh;
  min-height: 100svh;
}

.title {
  z-index: 1;
  grid-row: 3;
  grid-column: 1/5;
  padding: 1.6rem 0.8rem;
  margin: 0;
  background: oklch(0% 0 0deg);
  font-weight: 200;
  color: oklch(100% 0 0deg);
  text-align: right;
}
.title strong {
  display: block;
  font-weight: 700;
}

.title-block {
  z-index: 1;
  grid-row: 3;
  grid-column: 5;
  background: oklch(0% 0 0deg);
}

.quote {
  padding: 0.3rem;
  font-size: 2rem;
  white-space: nowrap;
  text-align: center;
}

.quote1 {
  grid-row: 4/9;
  grid-column: 2;
  background: oklch(45.98% 0.177 23.89deg);
  font-weight: 700;
  color: oklch(0% 0 0deg);
}

.quote2 {
  display: flex;
  grid-row: 1/5;
  grid-column: 3;
  justify-content: center;
  align-items: flex-end;
  background: oklch(51.25% 0.208 26.66deg);
  font-size: 2.5rem;
  font-weight: 700;
  color: oklch(100% 0 0deg);
  letter-spacing: 0.1rem;
}

.quote3 {
  grid-row: 5;
  grid-column: 3/5;
  padding: 0.3rem 0 0.3rem 0.5rem;
  font-size: 1.7rem;
  text-align: right;
}

.attribution {
  grid-row: 6;
  grid-column: 3/5;
  font-size: 1rem;
  text-align: right;
}

.uppercase {
  grid-row: 2;
  grid-column: 4;
  justify-self: flex-end;
  padding: 0.5rem 0;
  writing-mode: vertical-rl;
}
.uppercase strong {
  display: block;
}

.lowercase {
  grid-row: 1/3;
  grid-column: 2;
  padding: 0.4rem;
  background: oklch(45.98% 0.177 23.89deg);
  font-weight: 700;
  line-height: 1.2;
  writing-mode: vertical-rl;
  color: oklch(100% 0 0deg);
  text-align: right;
}

.numbers {
  display: flex;
  grid-row: 7;
  grid-column: 3/5;
  flex-direction: column;
  font-size: 1.1rem;
  font-weight: 700;
  writing-mode: vertical-rl;
}

.bars {
  display: flex;
  flex-grow: 1;
  justify-content: space-around;
  align-items: flex-end;
  block-size: 100%;
}

.bar {
  block-size: 100%;
  inline-size: 0.15rem;
  background: oklch(0% 0 0deg);
}
.bar:nth-child(odd) {
  inline-size: 0.3rem;
}
.bar:nth-child(3n) {
  block-size: 30%;
}
.bar:nth-child(3n+1) {
  block-size: 60%;
}
.bar:last-child {
  block-size: 100%;
